* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.flag_img {
  width: 100px;
  height: 40px;
  margin-right: 20px;
}

h3 {
  padding-bottom: 10px;
  padding-top: 10px;
  margin-top: 0;
  margin-bottom: 0;
}

.img_wraper {
  display: flex;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}
